import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { Modal } from 'antd';
import { IProps } from './type';
import styles from './index.less';

// 外部visible拥有绝对控制权
export default function Popup(props: IProps) {
  const {
    panel, // 弹窗面板
    children, // 弹窗包裹容器
    width = 1024,
    visible: _visible,
    onCancel,
  } = props;
  if (!panel) {
    return children;
  }
  // 控制弹窗展示
  const [visible, setVisible] = useState(_visible || false);
  // 控制弹窗销毁
  const [isActive, setIsActive] = useState(_visible || false);

  useEffect(() => {
    if (_visible !== undefined) {
      setVisible(_visible);
      if (_visible) {
        setIsActive(true);
      }
    }
  }, [_visible]);
  // 打开弹窗
  const onOpen = () => {
    setIsActive(true);
    setVisible(true);
  };
  // 关闭弹窗
  const onClose = () => {
    setVisible(false);
    if (onCancel) {
      onCancel();
    }
  };
  // 完全关闭后的回调
  const onAfterClose = () => setIsActive(false);

  // 渲染弹窗容器包含的孩子节点
  const renderChildren = () => {
    if (children) {
      return React.cloneElement(children as any, {
        onClick: onOpen,
      });
    }
    return undefined;
  };

  // 渲染弹窗组件
  const renderModal = () => {
    return (
      <Modal
        open={_visible !== undefined ? _visible : visible}
        onCancel={onClose}
        afterClose={onAfterClose}
        destroyOnClose
        centered
        wrapClassName={styles['popup-model']}
        width={width}
        title={null}
        footer={null}
        closable={false}
      >
        {panel && panel({
          onClose,
        })}
      </Modal>
    );
  };

  return (
    <>
      {renderChildren()}
      {isActive && renderModal()}
    </>
  );
}
