import React from 'react';
import classnames from 'classnames';
import { TAppItem } from '@/routes/type';
import { getSize } from '../tool';
import { formatDate, CONST } from './utils';
import styles from './app.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const Calendar = ({
  item,
  onClick,
}: IProps) => {
  const { w, h } = getSize(item.size);
  const { year, month, day, week = 0 } = formatDate();
  // 计算当月的天数
  const getCurDaysOfMonth = () => {
    const curDate = new Date();
    const curMonth = curDate.getMonth();
    const curTime = curDate.getTime();
    const nextTime = curDate.setMonth(curMonth + 1);
    return Math.ceil((nextTime - curTime) / (24 * 60 * 60 * 1000));
  };
  // 计算当月第一天是周几
  const getFirstDayWeek = () => {
    const curDate = new Date();
    curDate.setDate(1);
    return curDate.getDay();
  };
  const curDays = getCurDaysOfMonth();
  const firstDayWeek = getFirstDayWeek();
  const renderCalendar = (
    <div className={styles.calendar}>
      <div className={styles.calendarWeek}>
        {CONST.Week.map((i, index) => (
          <div key={i}><span>{i}</span></div>
        ))}
      </div>
      <div className={styles.calendarDays}>
        {new Array(firstDayWeek).fill('').map((o, oIndex) => (
          <div key={`o-${oIndex}`} />
        ))}
        {new Array(curDays).fill('').map((i, index) => (
          <div key={`i-${index}`}>
            <span className={classnames(index + 1 === day && styles.curDay)}>{index + 1}</span>
          </div>
        ))}
      </div>
    </div>
  );
  const renderChildren = () => {
    const yearData = w > 1 ? `${year}年${month}月` : `${year}/${month}`;
    const dayData = h === 1 && w > 1 ? `${month}/${day}` : day;
    const needYearAndMonth = h > 1;
    const needLongWeek = w >= 2 && h >= 2;
    const needSubPanel = h > 1 && w > 2;
    const needHeader = w === 2 && h === 2;

    return (
      <>
        <div
          className={classnames(
            styles.mainPanel,
            (h === 1 && w > 1) ? styles.row : styles.column,
            (w === 2 && h === 2) && styles['jc-between'],
            (h === 1 && w === 1) && styles['jc-center'],
            (w !== h) && styles['jc-evenly'],
            needSubPanel && styles.needSubPanel
          )}
          style={{
            width: needSubPanel ? '40%' : '100%',
            paddingBottom: needHeader ? 20 : 0,
          }}
        >
          {needYearAndMonth && (
            <div className={classnames(needHeader && styles.miniHeader)}>
              <div className={classnames(styles.f12)}>{yearData}</div>
            </div>
          )}
          <div
            className={classnames(
              styles.f24,
              (w === 1 && h > 1) && styles.f36,
              (w >= 2 && h >= 2) && styles.f48,
            )}
          >
            {dayData}
          </div>
          {needLongWeek ? (
            <div>
              <div className={classnames(styles.f12, styles['tc-gray'])}>第148天 第21周</div>
              <div className={classnames(styles.f12)}>
                {`四月初十 周${CONST.Week[week]}`}
              </div>
            </div>
          ) : (
            <div className={classnames((h === 1 && w > 1) ? styles.f16 : styles.f12, styles['tc-red'])}>
              {`星期${CONST.Week[week]}`}
            </div>
          )}
        </div>
        {needSubPanel && (
          <div className={classnames(styles.subPanel)}>
            {renderCalendar}
          </div>
        )}
      </>
    );
  };
  return (
    <div className={classnames(styles.container, w < h && styles.column)} onClick={onClick}>
      {renderChildren()}
    </div>
  );
};

export default Calendar;
