export { CONST };

/**
 * 日期类型
 */
type IDateProps = string | number | Date;
type IDateFormat = {
  year: number;
  month: number;
  day: number;
  week?: number;
};

/**
 * 格式化时间
 */
export const formatDate = (date?: IDateProps | IDateFormat): IDateFormat => {
  const _formatDate = (_date?: IDateProps) => {
    _date = !_date ? new Date() : new Date(_date);
    return {
      year: _date.getFullYear(), // 年
      month: _date.getMonth() + 1, // 月
      day: _date.getDate(), // 日
      week: _date.getDay(), // 星期几
    };
  };
  let { year, month, day }: any = date || {};
  if (!(month && year && day)) {
    return _formatDate(date as IDateProps | undefined);
  } else {
    return date as IDateFormat;
  }
};

/**
 * 根据指定日期获取星座
 */
const getConstellation = (date?: IDateProps | IDateFormat) => {
  const { month, day }: any = formatDate(date);
  const constellationString = CONST.Constellation.concat(CONST.Constellation[0]).join();
  const constellationDays = [20, 19, 21, 21, 21, 22, 23, 23, 23, 23, 22, 22];
  const wordage = CONST.Constellation[0].length;
  const wordageIndex = month * wordage - (day < constellationDays[month - 1] ? wordage : 0);
  return constellationString.substring(wordageIndex, wordageIndex + wordage);
};

/**
 * 根据指定日期获取生肖
 */
const getShengXiao = (date?: IDateProps | IDateFormat) => {
  const { year }: any = formatDate(date);
};
